import { Injectable } from '@angular/core';
import { CommonModule } from '@angular/common';

declare const $: any;

@Injectable()
export class DhtmlxWindowsPlugin {
  rootWins: any;

  constructor() {}

  getRootWindows(): any {

    if (!this.rootWins) {
      this.rootWins = new dhtmlXWindows();
      this.resizeViewport();
    }
    return this.rootWins;
  }

  resizeViewport(): void {
    const width = (window.innerWidth || document.body.clientWidth);

    let height: any = 0;
    height = Number((window.innerHeight || document.body.clientHeight));
    height = height <= 587 ? 587 : height - (height - 587);

    this.getRootWindows().setViewport(0, 0, width, height, 'webos-viewport');
  }

  getInstance(id: string, title: string): any {
    const rwins = this.getRootWindows();
    let w;

    if (rwins.isWindow(id)) {
      w = rwins.window(id);
      w.bringToTop();
    } else {
      w = this.getRootWindows().createWindow(id, 0, 0, 800, 500);
      w.setText(title);
      w.centerOnScreen();
      // w.keepInViewport(true);
    }

    return w;
  }

  closeOnTopWindow() {
    let isClose = true;
    this.getRootWindows().forEachWindow(function (w) {
      if (w.isOnTop() && isClose) {
        w.close();
        isClose = false;
      }
    });
  }
}



