import { Component, OnInit } from '@angular/core';
import { DhtmlxWindowsPlugin } from '../share/dhtmlx/dhtmlx-windows-plugin';

@Component({
  selector: 'app-settings',
  template: ``
})
export class SettingsComponent implements OnInit {

  constructor(private dhxWindows: DhtmlxWindowsPlugin) { }

  ngOnInit() {
  }

  open(): void {
    const wins = this.dhxWindows.getInstance('settingstWins', 'Settings');
    wins.button('minmax').hide();
    wins.button('park').hide();
    wins.denyResize();
    wins.showInnerScroll();
    wins.attachHTMLString('Coming Soon..');
  }

}



