import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { FormsModule } from '@angular/forms';

import { AppComponent } from './app.component';

import { DhtmlxGlobal } from './share/dhtmlx/dhtmlx-global';
import { DhtmlxWindowsPlugin } from './share/dhtmlx/dhtmlx-windows-plugin';

import { AppRoutingModule } from './app-routing.module';
import { WebosComponent } from './webos/webos.component';
import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './register/register.component';

@NgModule({
  imports: [
    BrowserModule,
    FormsModule,
    AppRoutingModule
  ],
  declarations: [
    AppComponent,
    LoginComponent,
    RegisterComponent,
    WebosComponent
  ],
  providers: [DhtmlxGlobal, DhtmlxWindowsPlugin],
  bootstrap: [AppComponent]
})
export class AppModule { }



