import { Component, OnInit, AfterViewInit } from '@angular/core';
import { DhtmlxWindowsPlugin } from '../share/dhtmlx/dhtmlx-windows-plugin';

import { ExplorerComponent } from '../explorer/explorer.component';
import { DashboardComponent } from '../dashboard/dashboard.component';
import { MonitoringComponent } from '../monitoring/monitoring.component';
import { SettingsComponent } from '../settings/settings.component';
import { HelpComponent } from '../help/help.component';

declare const $: any;

@Component({
  selector: 'app-webos',
  templateUrl: './webos.component.html',
  styleUrls: ['./webos.component.css'],
  providers: [
    DhtmlxWindowsPlugin,
    ExplorerComponent,
    DashboardComponent,
    MonitoringComponent,
    SettingsComponent,
    HelpComponent
  ]
})
export class WebosComponent implements OnInit, AfterViewInit {

  constructor(private dhxWindows: DhtmlxWindowsPlugin,
    private explorer: ExplorerComponent,
    private dashboard: DashboardComponent,
    private monitoring: MonitoringComponent,
    private settings: SettingsComponent,
    private help: HelpComponent
  ) {

    window.addEventListener('keydown', function (e) {
      if (e.keyCode === 27) {
        dhxWindows.closeOnTopWindow();
      }
    }, true);

    window.addEventListener('resize', function () {
      dhxWindows.resizeViewport();
    });
  }

  ngOnInit() {
    $('body').addClass('webos-body');
    $('body').addClass('webos-body-spinne');
  }

  ngAfterViewInit() {
    setTimeout(function () {
      $('#startSpinner').fadeOut('fast', function () {
        $('body').removeClass('.webos-body-spinne');
        $('#webos-task').fadeIn('slow');
        $('#webos-taskbar').fadeIn('slow');
        $('#githiub').fadeIn('slow');
      });
    }, 1000);
  }

  openWindow(menuId): void {
    this[menuId].open();
  }

  openStartWindow(menuId): void {
    this[menuId].open();
    $('#start').trigger('click');
  }

  logout(): void {
    alert('success logout');
    $('#start').trigger('click');
  }

}



