import { Component, OnInit } from '@angular/core';
import { DhtmlxWindowsPlugin } from '../share/dhtmlx/dhtmlx-windows-plugin';

@Component({
  selector: 'app-dashboard',
  template: ``
})
export class DashboardComponent implements OnInit {

  constructor(private dhxWindows: DhtmlxWindowsPlugin) { }

  ngOnInit() {
  }

  open(): void {
    const wins = this.dhxWindows.getInstance('dashboardWins', 'Dashboard');
    wins.maximize();
    wins.showInnerScroll();
    wins.attachURL('https://ecomfe.github.io/echarts-examples/public/editor.html?c=treemap-drill-down');
  }
}



